$color_prime: rgba(33, 150, 243, 1);
$color_prive_active: rgba(30, 136, 229, 1);
$color_def: rgba(255, 255, 255, 1);

@mixin shadow {
    box-shadow: 0px 6px 5px 0px rgba(0, 0, 0, 0.24);
}

@mixin textDefault () {
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0.25px;
}